import { useState, useEffect } from 'react';
import { fetchWordVerdict } from '../services/apiService';

type Persona = 'serious' | 'cheery';

interface GuessHistoryItem {
  guess: string;
  success: boolean;
  targetWord: string;
  globalCount?: number;
}

interface GuessResponse {
  success: boolean;
  message: string;
  globalCount?: number;
}

export function useGameState() {
  const [currentWord, setCurrentWord] = useState('Rock');
  const [score, setScore] = useState(0);
  const [guessHistory, setGuessHistory] = useState<GuessHistoryItem[]>([]);
  const [gameOver, setGameOver] = useState(false);
  const [processingGuess, setProcessingGuess] = useState(false);
  const [lastResponse, setLastResponse] = useState<GuessResponse | null>(null);
  const [persona, setPersona] = useState<Persona>('serious');

  // Reset game state
  const resetGame = () => {
    setCurrentWord('Rock');
    setScore(0);
    setGuessHistory([]);
    setGameOver(false);
    setLastResponse(null);
  };

  // Process a guess
  const submitGuess = async (guess: string) => {
    setProcessingGuess(true);
    
    try {
      // Check if the guess has already been made
      const isDuplicate = guessHistory.some(item => 
        item.guess.toLowerCase() === guess.toLowerCase()
      );
      
      if (isDuplicate) {
        setGameOver(true);
        setLastResponse({
          success: false,
          message: `Game Over! You already guessed "${guess}" before.`
        });
        setProcessingGuess(false);
        return;
      }
      
      // Call the API to check if the guess beats the current word
      const response = await fetchWordVerdict(guess, currentWord, persona);
      
      // Update game state based on API response
      if (response.success) {
        setScore(prev => prev + 1);
        setCurrentWord(guess); // The new guess becomes the word to beat
      }
      
      // Add to history
      setGuessHistory(prev => [
        ...prev, 
        {
          guess,
          success: response.success,
          targetWord: currentWord,
          globalCount: response.globalCount
        }
      ]);
      
      // Update response message
      setLastResponse(response);
    } catch (error) {
      setLastResponse({
        success: false,
        message: 'Sorry, an error occurred. Please try again.'
      });
    } finally {
      setProcessingGuess(false);
    }
  };

  return {
    currentWord,
    score,
    guessHistory,
    gameOver,
    processingGuess,
    lastResponse,
    resetGame,
    submitGuess,
    persona,
    setPersona
  };
}